'use client';

import Link from 'next/link';
import { usePathname, useSearchParams } from 'next/navigation';
import { ProductsType } from '@/types/ProductsType';
import styles from './BackLink.module.scss';

type Props = {
  category?: ProductsType | '/';
};

export default BackLink = ({ category = '/' }: Props) => {
  const pathname = usePathname();
  const searchParams = useSearchParams();

  // приклад: якщо є параметр "from" у query, повертаємось назад
  const from = searchParams.get('from') || `/${category}`;

  return (
    <Link href={from} className={`small-text ${styles['back-link']}`}>
      <span className='icon icon--arrow-left' />
      <p>Back</p>
    </Link>
  );
};
